.accordion-button {
  &:after {
	@extend .trevor-ti-minus:before;
	@apply mt-3 font-bold;
	font-size: rem-calc(15px);
  }

  .js-accordion:not(.show) & {

	&::after {
	  @apply mt-0 text-px18;
	  @extend .trevor-ti-plus:before;
	}
  }

  @screen md {
	&:after {
	  display: none!important;
	}
  }
}

.collapse {
  .accordion-body {
	@apply border-b border-blue_green border-opacity-20;

	@screen md {
	  @apply border-0;
	}
  }

  .js-accordion:not(.show) & {
	@apply hidden;

	@screen md {
	  @apply flex;
	}
  }
}
