// Tile Grid
.tile-grid {
  @apply pt-20 pb-px60 w-full;

  &-inner {
	@apply mx-auto text-center;
  }

  &-header {
	@apply mb-px50;
  }

  &-title {
	@apply font-semibold text-px32 leading-px42 mb-3.5 tracking-em_001;
  }

  &-desc {
	@apply text-px18 leading-px26;
	max-width: rem-calc(822px);
  }

  &-container {
	@apply grid grid-cols-1 gap-y-7 mb-10;
  }

  &.sm-accordion {
	.tile-grid-inner {
	  @apply px-0;
	}

	.tile-grid-header {
	  @apply container;
	}

	.tile-grid-container {
	  @apply text-left gap-y-0 bg-white md:grid md:grid-cols-2 md:gap-7 md:bg-transparent lg:grid-cols-3;
	}
  }

  @screen md {
	.tile-grid-inner {
	  @apply px-0;
	}

	&-header {
	  @apply mb-10;
	}

	&-title {
	  @apply mx-40;
	}

	&-desc {
	  @apply w-3/4 mx-auto;
	}

	&-container {
	  @apply grid-cols-2 gap-4;
	}
  }

  @screen lg {
	@apply pt-28 pb-48;

	&-header {
	  @apply mb-20;
	}

	&-title {
	  @apply text-px46 leading-px56;
	}

	&-desc {
	  @apply text-px26 leading-px36;
	}

	&-container {
	  @apply grid-cols-3;
	}
  }
}

.tile {
  @apply bg-white rounded-px10;
  @apply mx-auto px-7 pt-7 pb-16 w-full;
  @apply text-teal-dark text-left;
  max-width: theme('postCard.width.DEFAULT');
  height: rem-calc(370px);

  [data-whatintent='mouse'] &:hover {
	@apply bg-gray-light transition-colors;
  }

  &-title-top {
	@apply text-px16 leading-px24 tracking-em_001 font-semibold mb-3.5;
  }

  &-title {
	@apply text-px24 leading-px28 tracking-em001 font-semibold mb-3;
  }

  &-desc {
	@apply text-px16 leading-px24 mb-4;
  }

  &-cta {
