.666667 -49.54 6C-44.8 12.6667 -40.06 12.6667 -35.32 6C-30.58 -0.666667 -25.84 -0.666667 -21.1 6C-16.36 12.6667 -11.62 12.6667 -6.88 6C-2.14 -0.666667 2.6 -0.666667 7.34 6C12.08 12.6667 16.82 12.6667 21.56 6C26.3 -0.666667 31.04 -0.666667 35.78 6C40.52 12.6667 45.26 12.6667 50 6' stroke='%23003A48' stroke-width='4' stroke-linecap='round'/%3E%3C/svg%3E%0A");
	  background-size: 15.5px 12.5px;
	}
  }

  &.clickable-card {
	position: relative;

	.card-link {
	  position: absolute;
	  display: block;
	  top: 0;
	  bottom: 0;
	  left: 0;
	  right: 0;
	  text-indent: -9999px;
	  z-index: 1000;
	}
  }

  @screen md {
	@apply shadow-md;
	max-width: theme('postCard.width.md');

	&-title {
	}

	&-desc {
	}

	&-cta-wrap {

	}
  }

  @screen lg {
	max-width: theme('postCard.width.lg');
	height: rem-calc(402px);

	&-title-top {
		@apply text-px18 leading-px24 mb-3;
	}

	&-title {
		@apply text-px26 leading-px36 mb-3;
	}

	&-desc {
		@apply text-px18 leading-px26 mb-4;
		display: -webkit-box;
		-webkit-line-clamp: 4;
		-webkit-box-orient: vertical;  
		overflow: hidden;
	}

	&-cta-wrap {
		@apply text-px20 leading-px26;
	}
  }
}
